body {
	margin: 0;

	font-family: 'Raleway', sans-serif;
	font-optical-sizing: auto;
	font-style: normal;
}

h1,
h2,
h3,
h4,
h5,
p {
	margin: 0;
	padding: 0;

	font-weight: unset;
	font-size: unset;
}

ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

a {
	color: inherit;
	text-decoration: inherit;
}
